.content{
    /*border: 2px solid black;*/
    margin: 50px;
    max-width: 95%;
    padding: 10px;
    text-align: center;
    font-size: 80px;
    font-weight: 100;
}

.buttons{
    background-color: black;
    color: aliceblue;
    margin-right: 1rem;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 40px;
    border-radius: 12px;
    text-align: center;
    font-weight: 500;
    font-size: 15px;
}

.buttons:hover{
    background-color: rgb(47, 47, 47);
}
.buttons:active{
    background-color: rgb(143, 143, 143);
}

